#root {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

#app {
  margin: 0 auto;
  width: 340px;
  height: 280px;
  display: flex;
  flex-direction: column;
  justify-content: stretch;
  align-items: center;
  border-radius: 8px;
  border: thin solid black;
}

button {
  height: 100%;
  border-radius: 8px;
  border: thin solid black;
}

.gray {
  background-color: #2c3336;
  color: lightgray;
}

.red {
  background-color: red;
  color: lightgray;
}

.cadetblue {
  background-color: cadetblue;
  color: lightgray;
}

#expression, #display {
  height: 1em;
  font-size: 2em;
  width: 100%;
  text-align: right;
  overflow: hidden;
}

#display {
  font-weight: bold;
}

#expression {
  margin-bottom: 4px;
}

#screen {
  width: 98%;
  height: 100px;
  margin-right: 4px;
  margin-left: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 8px 0;
}
